.all-artists {
  width: 81.2%;

  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 2vw;
  
  margin: 0 auto;

  &__item {
    position: relative;

    &:hover .all-artists__item_wrapper {
      opacity: 1;
    }

    &_wrapper {
      padding: 0 2vw;
      opacity: 0;

      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;

      background-color: rgba(40, 40, 40, 0.79);
      color: #fff;

      position: absolute;
      z-index: 1;
      top: 50%;
      left: 50%;

      width: 84%;
      height: 85%;
      
      transition: opacity 0.3s linear;
      transform: translate(-50%, -50%);
    }
  }

  &__img {
    width: 100%;
    height: 100%;
  }

  &__name {
    font-size: 2em;
    letter-spacing: 0.25vw;
    text-align: center;

    padding-bottom: 2vw;
  }

  &__style {
    font-family: "Neris SemiBold", arial;
    font-size: 0.67rem;
    letter-spacing: 0.1vw;

    padding-bottom: 1.5vw;
  }
}